*{
  margin: 0;
  padding: 0;
  text-decoration: none;
  border: 0;
  list-style: none;
}
body{
  background-color: chocolate;
  color: darkorchid;
}
header{
  margin: 5% auto;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  background-color: aquamarine;
}
.border{
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 5px solid black;
  width: 40%; height: 10rem;
  background-color: wheat;
}
.Text{
  margin: 5rem auto;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.Card{
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
}
.Card ul{
border: 5px solid royalblue;
width: 100vw;
align-items: center; justify-content: center; display: flex;
padding: 2rem; margin: 2rem;
justify-content: space-around;
}
.Card ul li{
  transition: .5s all;
  width: 10rem;
  height: 10rem;
  border: 2px solid black;
  background-color: wheat;
  align-items: center; justify-content: center; display: flex; text-align: center;
}
.Card ul li:hover{
  transform:translateY(-2rem)
}

